import { createActionHandlers } from '@ali/4ever-cangjie';
import type { NodeSelectToolbarActionPayload } from '@ali/4ever-cangjie';
import { CommentItem, ShowCommentOptions } from '../types';

export const ACTION_COMMENT_CLICK = Symbol('comment-click');
export const ACTION_COMMENT_ADD = Symbol('comment-add');
export const ACTION_COMMENT_SHOW = Symbol('comment-show');
export const ACTION_COMMENT_CANCEL = Symbol('comment-cancel');
export const ACTION_COMMENT_SET_ID = Symbol('comment-setid');
export const ACTION_COMMENT_SET_COMMENTS = Symbol('comment-comments');
export const ACTION_COMMENT_SET_SIDE_BAR = Symbol('comment-set-sidebar');
export const ACTION_COMMENT_SELECTED_NODES = Symbol('comment-selected-nodes');

/**
 * 显示评论
 */
export const showComment = (contentId?: string, options?: ShowCommentOptions) => ({
  type: ACTION_COMMENT_SHOW,
  payload: { contentId, options },
});

/**
 * 隐藏评论
 */
export const hideComment = () => ({
  type: ACTION_COMMENT_SHOW,
  payload: undefined,
});

/**
 * 添加评论
 */
export const addComment = () => ({
  type: ACTION_COMMENT_ADD,
});

/**
 * 取消正在添加的评论
 */
export const cancelComment = () => ({
  type: ACTION_COMMENT_CANCEL,
});

/**
 * 设置显示的 comment （showComment 会触发滚动，这里没有副作用）
 */
export const setActiveId = (
  contentId: string | undefined,
  isAutoFocus = true,
) => ({
  type: ACTION_COMMENT_SET_ID,
  payload: { contentId, isAutoFocus },
});

/**
 * 设置 comments 数据
 */
export const setComments = (comments: CommentItem[]) => ({
  type: ACTION_COMMENT_SET_COMMENTS,
  payload: { comments },
});

/**
 * 设置右侧栏是否可见
 */
export const setSidebarVisible = (visible: boolean) => ({
  type: ACTION_COMMENT_SET_SIDE_BAR,
  payload: { visible },
});

export const [commentSelectedNodes, isCommentSelectedNodes] =
  createActionHandlers<NodeSelectToolbarActionPayload>(ACTION_COMMENT_SELECTED_NODES);
